<template>
  <v-container>
    <v-layout row wrap justify-space-between>
      <v-spacer></v-spacer>
      <v-dialog v-model="clearDialog" persistent max-width="290">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark outlined v-on="on">Clear Menu</v-btn>
        </template>
        <v-card>
          <v-card-title class="headline"
            >Are you sure you want to clear the menu?</v-card-title
          >
          <v-card-actions>
            <v-btn color="green darken-1" text @click="clearDialog = false"
              >Cancel</v-btn
            >
            <v-spacer></v-spacer>
            <v-btn color="red darken-1" text @click="clearCart"
              >Clear it!</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
    <v-layout row wrap justify-space-between>
      <v-flex mt-3 xs12>
        <v-data-table
          :headers="headers"
          :items="viewableItems"
          :items-per-page="1000"
          item-key="id"
          class="elevation-1 narrower-columns"
          disable-sort
          hide-default-footer
          show-expand
        >
          <template v-slot:item.action="{ item }">
            <v-icon large @click="removeMenuItem({ item: item })">
              mdi-cart-remove
            </v-icon>
          </template>
          <template v-slot:item.image="{ item }">
            <v-img
              :src="
                `https://spoonacular.com/recipeImages/${item.id}-90x90.${item.imageType}`
              "
              max-width="90"
            ></v-img>
          </template>
          <template v-slot:item.preparationMinutes="{ item }">
            {{ item.preparationMinutes | showDash }}
          </template>
          <template v-slot:item.cookingMinutes="{ item }">
            {{ item.cookingMinutes | showDash }}
          </template>
          <template v-slot:item.readyInMinutes="{ item }">
            {{ item.readyInMinutes | showDash }}
          </template>
          <template v-slot:item.stepCount="{ item }">
            {{
              item.analyzedInstructions
                ? item.analyzedInstructions
                    .map(instructions => instructions.steps.length)
                    .reduce((acc, stepCount) => acc + stepCount, 0)
                : "N/A"
            }}
          </template>
          <template v-slot:item.sourceUrl="{ item }">
            <a :href="item.sourceUrl">
              {{ item.sourceUrl | getHostname }}
            </a>
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length" class="pa-4">
              <h3>Ingredients</h3>
              <ul>
                <li
                  v-for="(ingredient, index) in [
                    ...item.usedIngredients,
                    ...item.missedIngredients
                  ]"
                  :key="index"
                >
                  {{ ingredient.original }}
                </li>
              </ul>
              <h3>Steps</h3>
              <ul>
                <li
                  v-for="(step, index) in [].concat(
                    ...item.analyzedInstructions.map(group =>
                      [].concat(...group.steps).map(step => step.step)
                    )
                  )"
                  :key="index"
                >
                  {{ step }}
                </li>
              </ul>
            </td>
          </template>
        </v-data-table>
        <!-- <div class="text-center pt-2">
          <v-pagination
            :value="lastResultsPage"
            :length="lastResultsPageCount"
            :total-visible="10"
            @input="runQuery"
          ></v-pagination>
        </div> -->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<style>
.narrower-columns td:not(:first-child):not(:last-child) {
  padding: 0 8px;
}
.narrower-columns td:first-child {
  padding-left: 16px;
  padding-right: 8px;
}
.narrower-columns td:last-child {
  padding-left: 8px;
  padding-right: 16px;
}
</style>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "MenuCart",

  computed: {
    ...mapState(["menuItems"]),
    viewableItems: function() {
      return Object.values(this.menuItems);
    }
  },

  methods: {
    ...mapMutations(["removeMenuItem", "clearMenuItems"]),
    clearCart: function() {
      this.clearDialog = false;
      this.clearMenuItems();
    }
  },

  filters: {
    getHostname: function(value) {
      if (!value) return "";
      return new URL(value).hostname;
    },
    showDash: function(value) {
      return value ? value : "-";
    }
  },

  data: () => ({
    clearDialog: false,
    headers: [
      { text: "", value: "action" },
      {
        align: "left",
        value: "image"
      },
      {
        text: "ID",
        align: "left",
        sortable: false,
        value: "id",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TITLE",
        align: "left",
        sortable: false,
        value: "title",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "PREP TIME",
        align: "left",
        sortable: false,
        value: "preparationMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "COOK TIME",
        align: "left",
        sortable: false,
        value: "cookingMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "READY IN",
        align: "left",
        sortable: false,
        value: "readyInMinutes",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "TOTAL STEPS",
        align: "left",
        sortable: false,
        value: "stepCount",
        class: ["font-weight-black", "subtitle-3"]
      },
      {
        text: "SOURCE",
        align: "left",
        sortable: false,
        value: "sourceUrl",
        class: ["font-weight-black", "subtitle-3"]
      },
      { text: "", value: "data-table-expand" }
    ]
  })
};
</script>
